[data-modal] {
	position: fixed;
	z-index: -1;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	visibility: hidden;
	min-width: 320px;
	@include vertical;
	@include gpu;
	overflow: auto;
	transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
	&.is-open {
		opacity: 1;
		visibility: visible;
		z-index: 100; } }

[data-modal-container] {
	position: relative;
	z-index: 1;
	font-size: 14px;
	vertical-align: middle;
	display: inline-block; }
[data-modal-close] {
	position: absolute;
	background-color: transparent;
	font-size: 0;
	.icon {
		font-size: 16px; } }

.modal {
	padding: 20px;
	color: $black;
	background-color: rgba($black, .2);
	.link {
		font-size: inherit; }
	&__close {
		top: -20px;
		right: -20px;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $white;
		box-shadow: 0 3px 6px rgba(#000, .16);
		@include hover {
			.icon {
				color: $lightBrown; } }
		.icon {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			font-size: 16px;
			transition: color .3s; } }
	&__off {
		top: 12px;
		right: 12px;
		.icon {
			color: $silver; } }
	&__title {
		margin-bottom: 40px;
		color: $black;
		@include f(28px,400,1);
		@include xs {
			margin-bottom: 24px; }
		&_wide {
			margin: 0 -100px 40px; } }
	&__container {
		width: 100%;
		max-width: 440px;
		font-size: 12px;
		padding: 55px 80px 50px;
		-webkit-box-shadow: 0 6px 12px rgba(#000, .16);
		box-shadow: 0 6px 12px rgba(#000, .16);
		background-color: $white;
		&_feedback {
			max-width: 1008px;
			background-color: $white;
			padding: 44px 104px 40px;
			@include sm {
				padding: 40px 20px; } }
		@include xs {
			padding: 40px 24px; } }
	&__info {
		@include f(14px,400,1.286);
		margin-bottom: 40px;
		text-align: center; } }

.questionnaire {
	&__container {
		max-width: 1008px;
		padding: 40px 52px; }
	&__form {
		text-align: left; }
	&__title {
		text-align: center;
		margin-bottom: 35px; }
	&__hint {
		display: block;
		font-size: 13px; }
	&__top {
		padding: 17px 0 0;
		@include xs {
			padding-top: 5px; } }
	&__form-control {
		display: flex;
		@include xs {
			flex-direction: column; }
		&_textarea {
			display: block;
			.input_textarea {
				margin-top: 16px;
				@include xs {
					margin-top: 5px; } } } }
	.form-control {
		&:not(:first-child) {
			margin-top: 16px;
			@include xs {
				margin-top: 5px; } } }
	&__input-label {
		margin-right: 30px;
		min-width: 72px;
		flex-shrink: 0; }
	&__radio {
		padding-top: 12px;
		// .form-radio-label
		// 	+sm
		// 		display: block
		// 		margin: 0 0 16px
		// 	&:not(:last-child)
		// 		margin-right: 30px
		// .form-radio-group
		// 	&:not(:first-child)
		// 		margin-top: 40px
		// 		+xs
 }		// 			margin-top: 16px
	&__checkbox {
		margin: 40px 0; }
	&__submit {
		text-align: center;
		.btn {
			min-width: 280px;
			@include xs {
				min-width: 180px; }
			&:disabled {
				color: $black;
				cursor: default;
				background-color: #efefef;
				@include hover {
					background-color: #efefef; } } } }
	.col {
		&:not(:first-child) {
			@include md {
				margin-top: 16px; }
			@include xs {
				margin-top: 5px; } } } }

.invite {
	&__container {
		max-width: 1008px;
		padding: 40px 104px;
		text-align: left;
		@include md {
			padding: 40px 52px; } }
	&__content {
		font-size: 16px;
		line-height: 1.5;
		@include sm {
			font-size: 14px; }
		p {
			&:not(:first-child) {
				margin-top: 25px; } } }
	&__title {
		text-align: center;
		margin-bottom: 35px; } }

.registration-tab {
	&__container {
		padding: 43px 80px 40px; }
	&__title {
		margin-bottom: 32px; }
	.form-error {
		display: block;
		margin-right: -10px;
		margin-top: 6px;
		color: $red;
		@include f(11px, 400, 1.273);
		text-align: left; }
	input[name*='password'] ~ .form-error {
		display: block !important; } }


.dashed-separator {
	margin: 24px -52px;
	background-image: url("data:image/svg+xml;utf8,<svg viewbox='0 0 1 50' width='100%' height='1px' xmlns='http://www.w3.org/2000/svg'><line x1='0' y1='1' x2='100%' y2='1' stroke='#C0C0C0' stroke-dasharray='4 8'/></svg>");
	background-repeat: no-repeat;
	background-position: center;
	height: 1px;
	&_feedback {
		margin: 0 -102px; }
	@include xs {
		margin: 16px -24px 4px; } }

.input-label {
	display: inline-block;
	vertical-align: top;
	font-size: 14px;
	line-height: 1.285;
	padding: 12px 0;
	&__info {
		text-align: center; }
	&__divider {} }


.strength-meter {
	border: none !important;
	background: none !important;
	font-weight: 400 !important;
	font-size: 11px !important;
	padding: 4px 0 !important;
	display: block !important;
	text-align: left;
	color: $gray !important;
	display: none !important;
	margin-right: -20px;
	.has-success & {
		display: block !important; } }
