.loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 100000;
}

.loader__inner {
  animation: textAnimation 3s cubic-bezier(0.37, 0.01, 0.81, 1);
  will-change: left;
  padding: 20px;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  transform: translateX(-100%);
}

.loader__outer {
  position: absolute;
  top: 0;
  height: 100%;
  width: 150%;
  left: 0;
  animation: loader1 3s cubic-bezier(0.37, 0.01, 0.81, 1);
  transform: translateX(100%);
  z-index: 1000;
  overflow: hidden;
}

.loader__content {
  position: absolute;
  top: 50%;
  left: 33.333%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 20px;
}

.loader:before, .loader:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateX(100%);
  width: 150%;
}

.loader:before {
  background-color: rgba(39, 39, 39, 0.8);
  animation: loader1 3s cubic-bezier(0.37, 0.01, 0.81, 1);
}

.loader:after {
  background-color: #E61739;
  animation: loader2 3s cubic-bezier(0.37, 0.01, 0.81, 1);
}

@keyframes loader1 {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes loader2 {
  0% {
    transform: translateX(135%);
  }
  100% {
    transform: translateX(-115%);
  }
}

@keyframes textAnimation {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

