.video-play,
.video-expand,
.video-pause,
.video-compress {
	display: inline-block;
	background-color: transparent;
	width: 40px;
	height: 40px;
	position: relative;
	.icon {
		@include vcenter;
		font-size: 32px;
		color: $white; } }


.video-play,
.video-compress {
	display: none; }

.is-fullscreen {
	.video-expand {
		display: none; }
	.video-compress {
		display: block; } }

.is-paused {
	~ .package-offer__video-buttons .video-play {
		display: block; }
	~ .package-offer__video-buttons .video-pause {
		display: none; } }



