.form {
	&__text {
		margin: 20px -27px 0;
		font-size: 14px;
		&_gray {
			color: $gray; } } }
.form-control {
	position: relative;
	&:not(:first-child) {
		margin-top: 24px; }
	&_sm {
		&:not(:first-child) {
			margin-top: 16px; } }
	&_restore-password {
		.form-error {
			display: block !important; } }
	&.is-checked {
		.icon-check {
			visibility: visible;
			opacity: 1; } }
	.icon-check {
		position: absolute;
		top: 50%;
		right: 8px;
		margin-top: -5px;
		font-size: 10px;
		color: $green;
		visibility: hidden;
		opacity: 0; } }


.form-control.has-success {
	.icon-check {
		opacity: 1;
		visibility: visible;
		margin-top: -18px; } }

.form-label {
	@include f(16px,400,1.5);
	display: block; }
.form-group {
	&:not(:last-child) {
		margin-bottom: 26px; } }
.form-tooltip {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 100%;
	z-index: 2;
	width: 320px;
	padding: 16px;
	font-size: 13px;
	color: $black;
	margin-left: 24px;
	box-shadow: 0 6px 12px rgba(#000, .16);
	background-color: $white;
	align-items: center;
	display: none;
	.tooltip-active & {
		display: flex;
		@include md {
			display: none; } }
	&:before {
		position: absolute;
		content: "";
		z-index: -1;
		top: 50%;
		left: -6px;
		margin-top: -6px;
		width: 13px;
		height: 13px;
		box-shadow: 0 6px 12px rgba(#000, .16);
		@include rotate(45);
		background-color: $white; }
	&:after {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		bottom: 0;
		width: 7px;
		background-color: $white; }
	&__icon {
		margin-right: 12px;
		height: 100%;
		font-size: 0;
		vertical-align: middle;
		display: inline-block;
		.icon {
			font-size: 20px;
			color: $lightBrown; } } }
.form-question {
	margin-right: -16px;
	text-align: right;
	@include sm {
		margin-right: 0; }
	&:not(:first-child) {
		margin-top: 20px; } }
.form-radio-group {
	&_vertical {
		&:not(:first-child) {
			margin-top: 40px;
			@include xs {
				margin-top: 16px; } }
		.form-radio-label {
			@include sm {
				display: block;
				margin-bottom: 16px; }
			@include xs {
				margin-bottom: 0; }
			&:not(:last-child) {
				margin-right: 30px;
				@include sm {
					margin-right: 0; } } }
		.radio {
			@include xs {
				margin-top: 16px; } } } }

.form-radio-label {
	@include f(14px,400,1.28);
	color: $gray;
	vertical-align: top;
	display: inline-block;
	&:not(:last-child) {
		margin-right: 16px; } }
.radio {
	position: relative;
	cursor: pointer;
	user-select: none;
	vertical-align: top;
	display: inline-block;
	&:not(:last-child) {
		margin-right: 12px; }
	input {
		position: absolute;
		opacity: 0;
		&:checked ~ .radio__text {
			&:before {
				border-color: $lightBrown; }
			&:after {
				opacity: 1; } } }
	&__text {
		padding-left: 22px;
		@include f(14px,400,1.28);
		display: block;
		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			width: 16px;
			height: 16px;
			border-radius: 50%;
			transition: border-color .3s;
			border: 1px solid $silver; }
		&:after {
			position: absolute;
			content: "";
			top: 4px;
			left: 4px;
			width: 8px;
			height: 8px;
			opacity: 0;
			transition: opacity .3s;
			border-radius: 50%;
			background-color: $lightBrown; } } }
.input {
	padding: 12px;
	height: 40px;
	width: 100%;
	color: $black;
	font-family: $montserrat;
	@include f(14px, 400, 1.285);
	border: 1px solid $silver;
	transition: border-color .3s;
	display: block;
	&::placeholder {
		color: #A29F9F; }
	&:focus {
		&::placeholder {
			font-size: 0; } }
	&_range {
		padding-left: 34px;
		&:focus {
			border: 1px solid $lightBrown; } }
	&.error {
		border-color: $red !important; }
	&_password {
		padding-right: 38px; }
	&_textarea {
		resize: none;
		padding: 10px; } }

.password-show {
	position: absolute;
	top: 50%;
	right: 12px;
	margin-top: -11px;
	font-size: 0;
	color: #C0C0C0;
	transition: color .3s;
	background-color: transparent;
	@include hover {
		color: $black; }
	.icon {
		font-size: 22px;
		line-height: 1;
		&-eye-closed {
			.form-control.is-show & {
				display: none; } }
		&-eye-opened {
			display: none;
			.form-control.is-show & {
				display: block; } } } }

.form-social {
	text-align: center;
	&:not(:first-child) {
		margin-top: 34px; }
	&_sm {
		&:not(:first-child) {
			margin-top: 16px; } } }
.form-footer {
	&:not(:first-child) {
		margin-top: 22px; }
	&_lg {
		&:not(:first-child) {
			margin-top: 30px; } }
	.btn-link {
		display: none; } }

.form-checkbox {
	margin-right: -60px;
	text-align: right;
	@include sm {
		margin-right: 0; }
	&:not(:first-child) {
		margin-top: 20px; }
	&_lg {
		margin-right: -29px;
		&:not(:first-child) {
			margin-top: 24px; } } }

.checkbox {
	position: relative;
	padding-left: 28px;
	text-align: left;
	@include f(13px,400,1.38);
	color: $gray;
	cursor: pointer;
	user-select: none;
	display: block;
	input {
		position: absolute;
		left: 0;
		top: -1px;
		opacity: 0;
		width: 16px;
		height: 16px;
		&:checked ~ .checkbox__icon {
			opacity: 1; } }
	&__text {
		&:before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			width: 16px;
			height: 16px;
			border: 1px solid $silver; }
		.link {
			color: $black; } }
	&__icon {
		position: absolute;
		top: 4px;
		left: 3px;
		font-size: 0;
		opacity: 0;
		transition: opacity .3s;
		.icon {
			font-size: 8px;
			color: $lightBrown; } } }

.reg-number {
	margin: 20px -22px 0;
	font-size: 0;
	&__text {
		@include f(17px,400,1.3);
		color: #CFC7C7;
		vertical-align: top;
		display: inline-block; }
	&__input {
		padding: 0;
		height: 24px;
		width: 24px;
		font-size: 20px;
		text-align: center;
		vertical-align: top;
		display: inline-block;
		&:not(:first-child) {
			margin-left: 8px; } } }

.form-separator {
	margin: 41px -80px 0;
	text-align: center;
	position: relative;
	@include xs {
		margin: 41px -24px 0; }
	&:before,
	&:after {
		content: "";
		position: absolute;
		width: 100px;
		height: 1px;
		top: 50%;
		transform: translateY(-50%);
		background-color: $gallery;
		@include xs {
			width: 15%; } }
	&:before {
		left: 0; }
	&:after {
		right: 0; } }

[data-validation-error-msg-length] {
	& ~ .form-error {
		display: block!important;
		margin-top: 5px;
		font-size: 11px;
		color: $red;
		text-align: left; } }
.valid-message {
	display: none; }



.has-success ~ .form-footer {
	.btn-reset {
		display: none; }
	.btn-link {
		display: block; } }

.form-error {
	text-align: left; }
